<template>
  <div class="login wh100 flex_center hidden pr">
    <div class="wrap white flex">
      <div class="login_left">
        <img src="@/assets/login_left.jpg" class="login_left_img">
      </div>
      <div class="login_right flex1">
        <ul class="tabList pointer flex_middle">
          <li class="lh36 fs18 c6" :class="{active: activeIndex === 0}" @click="toggleTab(0)">账密登录</li>
          <el-divider direction="vertical" />
          <li class="lh36 fs18 c6" :class="{active: activeIndex === 1}" @click="toggleTab(1)">验证码登录</li>
        </ul>
        <el-form v-if="activeIndex === 0" ref="form" :model="form" :rules="rules" class="login-form">
          <el-form-item prop="account">
            <el-input v-model="form.account" type="text" placeholder="请输入账号" clearable prefix-icon="el-icon-user" @keyup.native.13="next" />
          </el-form-item>
          <el-form-item prop="pass">
            <el-input ref="pass" v-model="form.pass" type="password" placeholder="请输入密码" show-password prefix-icon="el-icon-lock" @keyup.native.13="submitForm('form')" />
          </el-form-item>
          <el-form-item>
            <div class="w100 between">
              <el-checkbox v-model="checked">下次自动登录</el-checkbox>
              <span class="forget c9 pointer" @click="forgetPass">忘记密码</span>
            </div>
          </el-form-item>
          <el-button type="primary" class="login_btn w100 fs18" @click="submitForm('form')">登录</el-button>
        </el-form>
        <el-form v-else ref="codeForm" :model="codeForm" :rules="codeRules" class="login-form">
          <el-form-item prop="account">
            <el-input v-model="codeForm.account" type="text" placeholder="请输入手机号" maxlength="11" clearable prefix-icon="el-icon-mobile" @input="codeForm.account = codeForm.account.replace(/\D/g, '')" />
          </el-form-item>
          <el-form-item prop="code">
            <div class="flex_middle">
              <el-input v-model="codeForm.code" type="text" placeholder="请输入验证码" maxlength="4" clearable prefix-icon="el-icon-message" @input="codeForm.code = codeForm.code.replace(/\D/g, '')" />
              <el-button v-if="!isSend" style="height: 40px;" class="active_color ml20" @click="getCode">{{ str }}</el-button>
              <el-button v-if="isSend" style="height: 40px;" class="ml20" disabled>{{ countStr }}</el-button>
            </div>
          </el-form-item>
          <el-form-item>
            <div class="w100 between">
              <el-checkbox v-model="checked">下次自动登录</el-checkbox>
            </div>
          </el-form-item>
          <el-button type="primary" class="login_btn w100 fs18" @click="submit('codeForm')">登录</el-button>
        </el-form>
        <edit-pass title="修改密码" :visible.sync="editPassDialog" width="400px" @submitEdit="submitEdit" />
      </div>
    </div>
  </div>
</template>

<script>
import EditPass from '@/layout/components/EditPassword'
import { sendsmsCode } from '@/api/common'
import { get, set } from '@/utils/storage'
import { mapActions } from 'vuex'

export default {
  components: {
    EditPass
  },
  data() {
    const validateMobile = (rule, value, callback) => {
      if (!value) {
        callback(new Error('请输入手机号'))
      } else {
        if (!/^1[3456789]\d{9}$/.test(value)) {
          callback(new Error('请输入正确的手机号'))
        } else {
          callback()
        }
      }
    }
    const validateRegcode = (rule, value, callback) => {
      if (!this.codeForm.account) {
        callback(new Error('请先输入手机号'))
      } else if (!this.code) {
        callback(new Error('请先获取短信验证码'))
      } else if (!value) {
        callback(new Error('请输入验证码'))
      } else {
        callback()
      }
    }
    return {
      isMultiple: false, // 是否多公司
      activeIndex: 0,
      form: {
        account: '',
        pass: ''
      },
      rules: {
        account: [{ required: true, message: '请输入账号', trigger: 'change' }],
        pass: [{ required: true, message: '请输入密码', trigger: 'change' }]
      },
      checked: true,
      codeForm: {
        account: '',
        code: ''
      },
      codeRules: {
        account: [{ required: true, validator: validateMobile, trigger: 'change' }],
        code: [{ required: true, validator: validateRegcode, trigger: 'change' }]
      },
      code: '',
      str: '获取验证码',
      countStr: '重新发送（60s）',
      isSend: false,
      timer: null,
      companyList: [],
      editPassDialog: false
    }
  },
  created() {
    // 自动登录
    if (get(process.env.VUE_APP_STORAGE_USERINFO) && get('autoLogin') === 'true') {
      this.routerDump()
    }
  },
  methods: {
    ...mapActions('user', ['smslogin', 'loginFn']),
    next() {
      this.$refs.pass.focus()
    },
    toggleTab(i) {
      this.activeIndex = i
      if (i === 0) {
        this.$nextTick(() => {
          this.$refs.form.clearValidate(['account', 'pass'])
        })
      } else {
        this.$nextTick(() => {
          this.$refs.codeForm.clearValidate(['account', 'code'])
        })
      }
    },
    getCode() {
      this.$refs.codeForm.validateField('account', (err) => {
        if (!err) {
          sendsmsCode({ account: this.codeForm.account }).then(res => {
            if (res.code === 200) {
              this.code = 1
              this.isSend = true
              this.timer = null
              let time = 60
              this.timer = setInterval(() => {
                if (time === 0) {
                  this.str = '重新获取验证码'
                  this.countStr = '重新发送（60s）'
                  this.isSend = false
                  clearInterval(this.timer)
                } else {
                  time--
                  this.countStr = `重新发送（${time}s）`
                }
              }, 1000)
            }
          })
        }
      })
    },
    submitForm(formName) {
      this.$refs[formName].validate(async valid => {
        if (valid) {
          const res = await this.loginFn(this.form)
          if (res.code === 200) {
            if (this.timer) {
              clearInterval(this.timer)
            }
            this.login(res.data, 1)
          }
        } else {
          return false
        }
      })
    },
    submit(formName) {
      this.$refs[formName].validate(async valid => {
        if (valid) {
          const res = await this.smslogin(this.codeForm)
          if (res.code === 200) {
            if (this.timer) {
              clearInterval(this.timer)
            }
            this.login(res.data, 2)
          }
        } else {
          return false
        }
      })
    },
    login(res, type) {
      if (this.timer) { clearInterval(this.timer) }
      if (type === 1 && this.form.pass === '111111') {
        set('pass', 'default')
        this.editPassDialog = true
        return false
      }
      this.routerDump()
    },
    submitEdit() {
      this.routerDump()
    },
    forgetPass() {
      if (this.timer) { clearInterval(this.timer) }
      this.$router.push({ path: '/findpass' })
    },
    routerDump() {
      set('autoLogin', this.checked)// 自动登录
      set('pass', 'define')
      this.$router.replace('/client_list')
    }
  }
}
</script>

