    box-shadow: 0 1px 14px 2px rgba(45, 140, 240, 0.09);
    .login_left{
      width: 420px;
      height: 480px;
    }
    .login_left_img{
      border-radius: 10px 0 0 10px;
    }
    .login_right{
      padding: 40px 50px;
      .tabList{
        margin: 0;
        margin-bottom: 35px;
        li{
          &.active{
            font-weight: 600;
            color: #333;
            &::after{
              width: 100%;
              height: 2px;
              border-radius: 2px;
              background: #2d8cf0;
              content: "";
              display: block;
              position: relative;
              top: 0px;
              margin: 0 auto;
            }
          }
        }
        .el-divider--vertical{
          margin: 0 18px;
        }
      }
      .login-form{
        .el-input--medium .el-input__inner{
          height: 66px;
          line-height: 66px;
          border: none;
          border-radius: 0;
          border-bottom: 1px solid #e8e8e8;
        }
        .el-input__prefix{
          left: -5px;
        }
        .el-input--medium .el-input__icon{
          font-size: 17px;
          padding-top: 2px;
        }
        .forget{
          &:hover{
            color: #2d8cf0;
          }
        }
        .login_btn{
          height: 46px;
